.TerminauxList {
  .container-fluid {
    padding-top: 10px !important;
    background-color: #fff;
    @media (min-width: $minMd) {
      padding-top: 20px !important;
    }
  }
  &-filters {
    text-align: center;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    @media (min-width: $minMd) {
      text-align: left;
      display: block;
      margin-bottom: 20px;
    }
    .custom-select {
      width: 50%;
      background-color: #fff;
      border-color: #000;
      @media (min-width: $minMd) {
        width: auto;
      }
    }

    .btn-secondary {
      padding: 0 18px 0 14px;
      line-height: 36px;
      width: calc(50% - 0.3125rem);
      @media (min-width: $minMd) {
        width: auto;
      }
      svg {
        width: 18px;
        height: 18px;
        path {
          fill: #000;
          transition: 0.5s;
        }
      }

      &:hover {
        svg {
          path {
            fill: #fff;
          }
        }
      }

    }

    .mx-1 {
      margin-right: 0 !important;
      @media (min-width: $minMd) {
        margin-right: initial !important;
      }
    }
  }
  &-title {
    font-size: 16px;
    margin-bottom: 8px !important;
  }
  &-results-count {
    margin-bottom: 30px;
    font-weight: bold;
    span {
      color: $colorOrange;
    }
  }
  &-list > div {
    padding-bottom: 10px;
    display: none;
    @media (min-width: $minMd) {
      padding-bottom: 20px;
    }
  }
  &-bottom {
    .btn {
      display: block;
      margin-left: auto;
      margin-right: auto;
      margin-top: 10px;
      width: 100%;
      @media (min-width: $minMd) {
        width: auto;
      }

    }
  }
  &-listItemsColumn {
    display: flex;
    flex-direction: column;
  }

  .offreListeCol {
    display: none;
    flex-direction: column;
  }
}
