.Offre {
  &-detail-item {

    // Affiché dans le bloc détail lorsqu'il n'y a qu'une offre dans la page
    .OffreVariation-description {
      margin-bottom: 33px;
      padding-right: 100px;
      > div {
        font-size: 16px !important;
      }
    }
  }

  &-content {
    @media (min-width: $minLg) {
      padding-right: 100px;
    }
    flex: 0 0 60%;
    padding-right: 0;

    p {
      margin-bottom: 11px;
    }
  }

  &-title {
    font-weight: bold;
    font-size: 30px;
    margin-bottom: 18px;
  }

  &-video {
    font-weight: bold;
    margin-bottom: 34px;
    display: flex;
    align-items: center;
    text-decoration: underline;

    img {
      padding-right: 10px;
      transform: scale(0.75);
      height: 18px;
    }
  }
}
