.TerminauxLightbox {
  &-innerWrapper {
    padding: 20px;
  }

  &-close {
    text-align: right;
  }

  .TerminauxDetailsPreview {

    &-mainImg,
    &-mainImage {
      overflow: hidden;
      padding-bottom: 40px;
      padding-top: 12px;
      width: 100%;
      text-align: center;
      cursor: pointer;
      position: relative;

      .swiper-slide {
        img {
          max-width: 100%;

        }
      }
    }
  }

  .TerminauxDetailsPreview-thumbnail-item {
    width: 23%;
    padding-bottom: 23%;
  }
}
