.Top-vente {
  overflow: hidden;
  padding-top: 20px;

  @media (min-width: $minLg) {
    margin-bottom: 10px;
  }

  &-list {
    display: flex;
    justify-content: space-between;
  }

  &-item {
    padding: 13px;
    height: 100%;
    display: flex;

    @media (min-width: $minSm) {
      border: 1px solid $colorGrayLine;
    }
    @media (min-width: $minLg) {
      display: block;
      flex-direction: column;
      text-align: center;
      background-color: #fff;
    }
  }

  &-photo {
    margin-bottom: 12px;
    max-width: 25%;

    img {
      max-width: 50% !important;
    }

    @media (min-width: $minLg) {
      max-width: 100%;
    }
  }

  &-content {
    display: flex;
    flex: 1;
    align-items: center;
    margin-left: -25px; // Pour resserer entre l'image et le texte
    @media (min-width: $minLg) {
      justify-content: center;
      margin-left: 0;
      margin-top: -10px;
    }
  }

  &-title {
    font-size: 18px;
    font-weight: bold;
    line-height: 0.9em;
    margin: 2px 0;
    @media (min-width: $minLg) {
      font-size: 24px;
      margin: 12px 0 2px 0;
    }
  }

  &-mate {
    display: inline-block;
    margin-left: 7px;
    @media (min-width: $minLg) {
      display: none;
    }
  }
  
  &-subtitle {
    margin: 2px 0;
    display: none;
    @media (min-width: $minLg) {
      display: block;
    }
  }

  &-price {
    color: $colorOrange;
    font-size: 20px;
    text-transform: uppercase;
    margin-bottom: 4px;
  }

  &-aPartir {
    display: inline-block;
    text-transform: none;
    color: #000;
    font-size: 14px;
    margin-right: 7px;

    @media (min-width: $minLg) {
      display: none;
    }
  }

  &-arrow {
    display: flex;
    align-items: center;

    svg {
      width: 20px;
    }
  }

  &:hover {
    .Top-vente-title,
    .Top-vente-subtitle {
      color: #000;
    }
  }

}
