// Présent dans les tuiles de offre-variation
.OffreVariation{

  &-description {
    padding: 20px 0;
    @media (min-width: $minLg) {
      padding: 0;
    }
    > div {
      display: flex;
      padding-bottom: 20px;
      font-size: 15.2px;

      &:last-child {
        padding-bottom: 0;
      }

      span {
        display: block;
        padding-right: 20px;
      }
    }
  }
}
