.AssociatedProduct {
  position: relative;
  padding: 10px;
  border: 1px solid $colorGrayLine;
  display: flex;
  min-height: 100px;
  margin-bottom: 15px;
  cursor: pointer;

  .photo {
    flex: 0 0 20%;
    justify-content: center;
    align-items: center;
    display: flex;

    img {
      max-height: 80px;
    }
  }

  .texte {
    padding-right: 40px;
  }

  .bold {
    font-size: 18px;
    margin-bottom: 10px;
  }

  &-banner {
    position: absolute;
    top: -1px;
    right: -11px;

    &-triangle1 {
      border-color: transparent $colorGreenEmerald transparent transparent;
      position: absolute;
      top: 1px;
      right: 11px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 70px 70px 0;
    }

    &-triangle2 {
      position: absolute;
      top: 1px;
      right: 11px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 40px 40px 0;
      border-color: transparent #fff transparent transparent;
    }

    &-text {
      position: absolute;
      top: 22px;
      right: 3px;
      transform: rotate(45deg);
      font-size: 10px;
      width: 70px;
      text-align: center;
      font-weight: bold;
    }
  }
}


//// Styles du contenu dans la modale
.BlocAssociatedProductLightbox {
  &-innerWrapper {
    display: flex !important;
    padding: 20px;
  }

  &-image {
    margin-right: 25px;
    img {
      max-width: 100%;
    }
  }

  &-texte {
    .pageStyle-bold {
      margin-bottom: 10px;
    }
  }

  &-close {
    cursor: pointer;
  }
}

