.MenuMobile {
  position: relative;
  @media (min-width: $minLg) {
    display: none;
  }

  background-color: #000;

  .container {
    position: initial;
  }

  & > .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-brand {
    padding: 10px 0;
    svg {
      width: 30px;
      height: 30px;
    }
  }

  &-mainLinks {
    display: flex;
    list-style: none;
    flex-direction: row;
    align-items: flex-end;
    margin: 0;
    padding: 0;
    &-item {
      &:last-child a {
        padding-right: 0;
      }
    }
    &-link {
      padding: 0 10px;
      display: block;
      cursor: pointer;
    }
    &-svg {
      max-height: 26px;
      width: auto;
      position: relative;
      svg {
        path {
          fill: #fff;
        }
      }
    }
    .circle-number {
      width: 20px;
      height: 20px;
      position: absolute;
      right: 0;
      top: 0;
      transform: translate(35%, -50%);
      border-radius: 50%;
      background-color: $colorBlue;
      color: #fff;
      font-size: 12px;
      font-weight: bold;
      text-align: center;
      line-height: 20px;
      padding-left: 1px;
    }
    .burgerIcon {
      position: relative;
      width: 40px;
      height: 30px;
      cursor: pointer;
      span {
        position: absolute;
        left: 10px;
        width: 24px;
        height: 2px;
        border-radius: 3px;
        background-color: #fff;

        transform: rotate(0deg);
        transition: .25s ease-in-out;
        transform-origin: left center;

        &.burgerIcon-1 {
          top: 6px;
          left: 10px;
        }
        &.burgerIcon-2 {
          top: 14px;
          left: 10px;
        }
        &.burgerIcon-3 {
          top: 22px;
          left: 10px;
        }
      }
      &.open {
        .burgerIcon-1 {
          -webkit-transform: rotate(45deg);
          -moz-transform: rotate(45deg);
          -o-transform: rotate(45deg);
          transform: rotate(45deg);
          left: 14px;
          top: 5px;
        }
        .burgerIcon-2 {
          width: 0;
          opacity: 0;
          left: 14px;
        }
        .burgerIcon-3 {
          -webkit-transform: rotate(-45deg);
          -moz-transform: rotate(-45deg);
          -o-transform: rotate(-45deg);
          transform: rotate(-45deg);
          top: 22px;
          left: 14px;
        }
      }
    }
  }

  &-collapse {
    display: inline-table;
    position: absolute;
    height: 0;
    top: -100vh;
    left: 0;
    right: 0;
    width: 100%;
    background-color: #fff;
    z-index: -1;
    & > .container {
      background-color: #fff;
    }
  }

  &-search {
    form {
      padding: 10px 0;
      position: relative;
    }
    label {
      position: absolute;
      left: 9px;
      top: 18px;
      svg {
        width: 24px;
        height: 24px;
      }
    }
    input {
      width: 100%;
      border: 2px solid $colorGrayLine;
      height: 40px;
      line-height: 40px;
      padding: 0 10px 0 40px;
      &:focus {
        outline: none;
      }
    }
  }

  &-nav {
    list-style: none;
    margin: 0;
    padding: 0 0 10px 0;
  }

  &-nav-link {
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-weight: bold;
    padding: 11px 0;
    svg {
      width: 22px;
      height: 22px;
      padding-bottom: 2px;
    }
    &:hover {
      svg {
        polygon {
          fill: $colorOrange;
        }
      }
    }
  }

  &-nav-panel {
    position: absolute;
    display: inline-block !important;
    top: 0;
    width: 100%;
    left: 100%;
    background-color: #fff;
  }

  &-subHeader {
    border-bottom: 1px solid $colorGrayLine;
    &-link {
      display: inline-block;
      padding: 15px 0;
      width: 100%;
      font-size: 20px;
      font-weight: bold;
    }
    svg {
      width: 22px;
      height: 22px;
      padding-bottom: 2px;
    }
  }

  &-subNav {
    list-style: none;
    margin: 0;
    padding: 10px 0;
  }

  &-subItem {
    & > a {
      font-weight: bold;
      display: flex;
      width: 100%;
      padding: 10px 0;
      justify-content: space-between;
      svg {
        width: 24px;
        height: 24px;
        padding-bottom: 2px;
        margin-right: 10px;
      }
      .MenuMobile-subItem-svg svg {
        width: 22px;
        height: 22px;
        padding-bottom: 2px;
        margin-right: 0;
      }
    }
  }

  &-subFooter {
    border-top: 1px solid $colorGrayLine;
    border-bottom: 1px solid $colorGrayLine;
    a {
      font-weight: bold;
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding: 15px 0;
      svg {
        width: 22px;
        height: 22px;
        padding-bottom: 2px;
      }
    }
  }

  &-subSubNavWrapper {
    position: absolute;
    display: inline-block !important;
    top: 0;
    width: 100%;
    left: 100%;
    background-color: #fff;
  }

  &-subSubBack {
    display: inline-block;
    padding: 15px 0;
    width: 100%;
    font-size: 20px;
    font-weight: bold;
    border-bottom: 1px solid $colorGrayLine;
    cursor: pointer;
    svg {
      width: 22px;
      height: 22px;
      padding-bottom: 2px;
    }
  }

  &-subSubNav {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &-subSubItem {
    a {
      font-weight: bold;
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding: 11px 0;
      svg {
        width: 22px;
        height: 22px;
        padding-bottom: 2px;
      }
    }
  }

  &-subSubFooter {
    border-top: 1px solid #cccccc;
    border-bottom: 1px solid #cccccc;
    a {
      font-weight: bold;
      display: flex;
      -webkit-box-pack: justify;
      justify-content: space-between;
      width: 100%;
      padding: 15px 0;
      svg {
        width: 22px;
        height: 22px;
        padding-bottom: 2px;
      }
    }
  }

  &-bottom {
    &-mainLinks {
      display: inline-block;
      width: 100%;
      background-color: #000;
      color: #fff;
      border-top: 1px solid $colorGrayLine;
      padding: 10px 0;
      ul {
        list-style: none;
        margin: 0;
        padding: 0;
        li {

        }
      }
      a {
        color: #fff;
        display: flex;
        justify-content: space-between;
        padding: 11px 0;
        font-weight: bold;
        &:hover {
          color: $colorOrange;
          svg {
            polygon, path {
              fill: $colorOrange;
            }
          }
        }
      }
      .linkIconSvg > svg {
        width: 24px;
        height: 24px;
        padding-bottom: 2px;
        margin-right: 10px;
        path {
          fill: #fff;
        }
      }
      .angleRightSvg > svg {
        width: 22px;
        height: 22px;
        padding-bottom: 2px;
        polygon {
          fill: #fff;
        }
      }
    }
    &-particulierLink {
      display: inline-block;
      width: 100%;
      background-color: #000;
      color: #fff;
      border-top: 1px solid $colorGrayLine;
      padding: 5px 0;
      a {
        color: #fff;
        padding: 11px 0;
        display: flex;
        justify-content: space-between;
        font-weight: bold;
        &:hover {
          color: $colorOrange;
          svg {
            polygon {
              fill: $colorOrange;
            }
          }
        }
      }
      svg {
        width: 22px;
        height: 22px;
        padding-bottom: 2px;
        polygon {
          fill: #fff;
        }
      }
    }
    &-langLinks {
      display: inline-block;
      width: 100%;
      background-color: #000;
      border-top: 1px solid $colorGrayLine;
      border-bottom: 1px solid $colorGrayLine;
      padding: 10px 0;
      text-align: right;
      ul {
        list-style: none;
        margin: 0;
        padding: 0;
        li {
          display: inline-block;
          &:last-child {
            a {
              padding-right: 5px;
            }
          }
        }
      }
      a {
        color: #fff;
        padding: 11px;
        display: inline-block;
        font-weight: bold;
        &.active {
          color: $colorOrange;
        }
      }
    }
  }

}

.MenuMobile .user-connexion-triangle {
  left: 50%;
  bottom: -10px;
}

.MenuMobile .ConnexionCart {
  top: 40px;
}

.MenuMobile .open {
  .user-avatar-circle {
    display: inline-block;
    position: relative;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: $colorGrayLight;
    margin-bottom: -4px;
    & > span {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    svg {
      max-height: 17px;
      path {
        fill: $colorGrayDark;
      }
    }
  }
}
