.TerminauxDetailsPreview {

  .swiper-wrapper, .swiper-container {
    margin-bottom: 0;
  }

  &-mainImg,
  &-mainImage {
    overflow: hidden;
    padding-bottom: 40px;
    padding-top: 12px;
    width: 100%;
    cursor: pointer;
    position: relative;

    .swiper-wrapper {
      display: flex;
      align-items: center;
    }

    .swiper-slide {
      iframe {
        max-width: 100%;
        height: auto;
      }
      img {
        max-width: 100%;
      }
    }
  }

  /* GABARIT VIDEO 16/9 */
  .gabarit-video {
    position: relative;
    img {
      width: 100%;
      height: auto;
      max-width: 100% !important;
    }
    iframe {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }

  &-thumbnail {
    &-list {
      display: flex;
      justify-content: space-between;
      overflow: hidden;
      margin-bottom: 20px;
    }
    &-item {
      width: 28%;
      padding-bottom: 28%;
      position: relative;
      .item-inside { // astuce pour avoir la hauteur egale a la largeur grace au padding du parent...
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: $colorGrayLight;
        cursor: pointer;
      }
    }
  }

  &-playIcon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    svg {
      width: 34px;
      height: 34px;
    }
  }

  &-imgWrapper {
    position: relative;
    display: inline-block;
  }

  &-zoomBtn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 35px;
    height: 35px;
    border-radius: 50%;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.35);
    background-color: #ffffff;
    span {
      position: absolute;
      top: 49%;
      left: 49%;
      transform: translate(-50%, -50%);
      svg {
        width: 20px;
        height: 20px;
      }
    }
  }

  &-videoBtn {
    font-weight: bold;
    text-decoration: underline;
    cursor: pointer;
    svg {
      width: 18px;
      height: 18px;
      padding-bottom: 3px;
      padding-right: 3px;
    }
  }
}
