.Flag {
  position: absolute;
  top: 0;
  right: 0;
  @media (min-width: $minMd) {
    right: 0;
  }
  &.flag-orange {
    .Flag-triangle1 {
      border-color: transparent $colorOrange transparent transparent;
    }
  }
  &.flag-blue {
    .Flag-triangle1 {
      border-color: transparent $colorBlue transparent transparent;
    }
  }
  &.flag-green {
    .Flag-triangle1 {
      border-color: transparent $colorGreenEmerald transparent transparent;
    }
    .Flag-text {
      color: #000;
    }
  }
  &-triangle1 {
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 70px 70px 0;
  }
  &-triangle2 {
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 40px 40px 0;
    border-color: transparent #ffffff transparent transparent;
  }
  &-icon {
    position: absolute;
    transform: rotate(45deg);
    top: 2px;
    right: 3px;
    svg {
      width: 20px;
      height: 20px;
      path {
        fill: #fff;
      }
    }
  }
  &-text {
    position: absolute;
    top: 22px;
    right: -8px;
    transform: rotate(45deg);
    font-size: 10px;
    width: 70px;
    text-align: center;
    font-weight: bold;
    color: #ffffff;
  }
}
