.OffreVariation {
  height: initial !important; // Annule le height 100% du swiper pour avoir les slides de la même hauteur
  transition: transform 500ms;
  display: flex;
  flex-direction: column;

  @media (min-width: $minLg) {
    &:hover {
      cursor: pointer;
      transform: scale(1.03);
    }
    .OffreVariation-content {
      &:hover {
        border: 1px solid $colorOrange;
      }
    }
  }


  &-isActive {
    @media (min-width: $minLg) {
      transform: scale(1.05);
      height: auto;
      &:hover {
        transform: scale(1.05);
      }
    }

    .OffreVariation-content {
      border: 2px solid $colorOrange;
      &:hover {
        border: 2px solid $colorOrange;
      }
    }
  }


  /* Header */
  &-header {
    position: relative;
    width: 100%;
    z-index: 10;
    display: flex;
    align-items: stretch;
    justify-content: space-around;
    flex-direction: column;
    height: 100%;

    min-height: 100px;
    padding: 20px 10px;

    /* TEST */
    min-height: 142px;
    flex: 0;

    background: $colorBlueCelestial;
    color: #000;

    @media (max-width: $maxMd) {
      margin: 0 auto;
    }

    &.OffreVariation-isFlash,
    &.OffreVariation-isPromo {
      background: #000;
      color: #fff;
      .OffreVariation-price {
        color: $colorOrange;
      }
    }

  }

  &-price {
    font-size: 28.5px;
    line-height: 1em;
    font-weight: bold;
    padding: 5px 0;

    > span {
      font-weight: initial;
      font-size: 16px;
      line-height: 1em;
      margin-left: 5px;
    }
  }

  &-titre {
    text-align: left;
    font-size: 15.2px;
  }

  &-engagement {
    font-size: 12px;
    display: none;
    margin-bottom: 15px;
  }

  /* Modificateurs */
  &-isFlash, &-isPromo {
    .OffreVariation-engagement {
      display: block;
    }

    .OffreVariation-price {
      font-size: 26.6px;
    }
  }

  &-isNouveau {
    background: $colorBlueCelestial;
    color:#000;
  }



  /* Content  */
  &-content {
    padding: 20px 10px;
    border: 2px solid $colorGrayLine;
    margin-top: 5px;

    /* TEST */
    flex: 1;

    @media (max-width: $maxMd) {
      padding-top: 0px;
      //margin-top: -30px;
    }

    .pageStyle-boldOrange {
      font-size: 18px;
      padding-bottom: 20px;
      @media (max-width: $maxMd) {
        padding-bottom: 0;
      }
    }

    .pageStyle-bold {
      font-size: 15.2px;
      color: #000;
    }

    .pageStyle-btn-forfait {
      display: none;
      @media (max-width: $maxMd) {
        display: block;
      }
    }

    .pageStyle-btn-mobile {
      display: none;
      @media (max-width: $maxMd) {
        display: block;
      }
    }
  }

  &-warning { //Include dans les vignettes pour le swiper mobile
    .Warning {
      display: none;
      @media (max-width: $maxMd) {
        display: block;
      }
    }
  }
}

