.Counter {
  display: flex;
  position: relative;
  border: 1px solid $colorGrayLine;
  border-radius: 4px;
  font-weight: bold;
  font-size: 14px;
  text-align: center;
  padding: 0 1px;
  &-display {
    width: 20px;
    height: 22px;
    z-index: 1;
    line-height: 22px;
  }
  &-separator {
    height: 22px;
    z-index: 1;
    line-height: 22px;
  }
  &-hline {
    position: absolute;
    left: 0;
    height: 1px;
    width: 100%;
    top: 50%;
    background-color: $colorGrayLine;
    z-index: 0;
  }
}
