.TerminauxDetailsChoice {

  .choice-envoi-mobile {
    display: block;
    @media (min-width: $minMd) {
      display: none;
    }
  }
  
  .choice-envoi {
    display: none;
    @media (min-width: $minMd) {
     display: block;
    }
  }
  
  @media (min-width: $minMd) {
    margin-top: 30px;
  }
  @media (min-width: $minLg) {
    margin-top: 0;
  }

  &-variations {
    display: flex;
    padding: 10px 0;
    border: 0;

    > div {
      flex: 1;
      padding: 14px 0;
    }

    @media (min-width: $minMd) {

      padding: 0;
      border: 1px solid $colorGrayLine;
      border-bottom: 0;

      > div + div {
        border-left: 1px solid $colorGrayLine;
      }
    }
  }

  &-cellTitle {
    width: 100%;
    padding: 0 0 14px 0;
    font-weight: bold;
    span {
      font-weight: normal;
      display: block;
    }
    @media (min-width: $minMd) {
      padding: 0 14px 14px 20px;
      span {
        display: inline-block;
      }
    }
  }

  &-stockage {
    padding-left: 0 !important;
    padding-right: 0 !important;

    @media (min-width: $minMd) {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }

    &-inner {
      font-size: 0;
      max-width: 180px;
      @media (min-width: $minMd) {
        padding-left: 14px;
      }
    }

    @media (min-width: $minMd) {
      justify-content: left;
    }

    .item {
      position: relative;
      padding:  8px 10px;
      border-radius: 22px;
      //min-width: 73px;
      text-align: center;
      margin: 3.5px;
      font-weight: bold;
      border: 2px solid #000000;
      font-size: 16px;
      display: inline-block;
      cursor: pointer;
      min-width: 75px;
      &.disabled {
        opacity: 0.4;
        cursor: not-allowed;
      }
      &.warning {
        opacity: 0.4;
      }

      &.active {
        border: 2px solid $colorOrange;

        .TerminauxDetailsChoice-stockage-warning {
          right: -1px;
        }
      }
    }

    &-warning {
      display: block;
      position: absolute;
      bottom: -5px;
      right: 0;
      width: 18px;
      height: 18px;
    }

  }

  &-colors {

    @media (min-width: $minMd) {
      display: flex;
      flex-wrap: wrap;
      justify-content: left;
    }

    .ColorCircle {
      cursor: pointer;
      &.disabled {
        opacity: 0.4;
        cursor: not-allowed;
      }
      &.warning {
        opacity: 0.4;
      }
    }

    &-inner {
      padding-left: 0;
      font-size: 0;
      max-width: 140px;
      @media (min-width: $minMd) {
        max-width: 180px;
        padding-left: 17px;
      }
    }

    .color-txt {
      font-size: 16px;
      transform: translateY(-100%);
      display: inline-block;
      margin-right: 8px;
    }

  }

  &-aPArtirDe {
    margin-bottom: 5px;
  }

  .price {

    display: none;
    position: relative;

    & > div {
      &:last-child {
        margin-bottom: 0;
      }
    }

    text-align: center;
    padding: 15px;
    @media (min-width: $minMd) {
      border: 1px solid $colorGrayLine;
    }

    .pageStyle-boldOrange {
      font-size: 32px;

      span:not(.reset-span) {
        font-size: 18px;
        text-transform: uppercase;
      }

    }

    .prix-mobile-avec-forfait {
      display: none;
    }

    .stock {
      margin: 10px 0;
      font-weight: bold;

      svg {
        height: 15px;
        width: 15px;
        margin-right: 5px;
      }

      &.available {
        svg path {
          fill: #3dcb3c;
        }
      }

      &.unavailable {
        color: $colorRed;

        svg path {
          fill: $colorRed;
        }
      }
    }

    .expedition {
      margin: 10px 0;

      svg {
        width: 22px;
        margin-right: 5px;
      }
    }

    .forfait {
      width: 100%;
      margin-top: 10px;
      margin-bottom: 20px;

      .caption {
        text-align: right;
        font-size: 12px;
      }

      .custom-control.custom-radio {
        margin-top: 2px;
        margin-bottom: 12px;
      }

      .custom-control-label {
        width: 100%;
        //display: flex;
        //justify-content: space-between;
        font-weight: bold;
        line-height: 25px;

        &::before, &::after {
          bottom: 2px;
          top: auto;
        }

        &::before {
          border-color: $colorGrayLine;
        }

        & > .option-title {
          line-height: normal;
          padding-top: 5px;
        }

        & > .option-price {
          font-size: 20px;
          color: $colorOrange;
          float: right;
        }
      }
    }


    .ProductFlag {
      top: -1px;
      right: -11px;
    }

  }

  .pageStyle-btn-forfait {
    width: 100%;
  }

  .forfait-mobile {
    margin-bottom: 23px;
    position: relative;
    &-row {
      display: flex;
    }
    &-svg {
      display: block;
    }
    &-title {
      font-weight: bold;
      height: 35px;
      display: block;
      line-height: 35px;
    }
    &-without,
    &-with {
      cursor: pointer;
      flex: 0 0 50%;
      &:hover {
        .active-line {
          //background-color: $colorOrange;
        }
      }
    }
    .active-line {
      width: 100%;
      height: 3px;
    }
    .active {
      .forfait-mobile-svg {
        svg path {
          fill: $colorOrange;
        }
      }
      .forfait-mobile-title {
        color: $colorOrange;
      }
      .active-line {
        background-color: $colorOrange;
      }
    }
    &-baseline {
      position: absolute;
      bottom: 0;
      height: 1px;
      width: calc(100% + 50px);
      margin-left: -25px;
      background-color: $colorGrayLine;
    }
  }

  &-alertSubscription {
    margin: 20px 0;
    &-input {
      margin-bottom: 12px;
      label {
        margin-bottom: 22px;
      }
    }
    &-info {
      margin-bottom: 24px;
    }
    &-submit {
      width: 100%;
    }
    &-success {
      display: none;
      font-size: 12px;
      padding: 10px;
      border: 2px solid #32c832;
      margin-bottom: 15px;
      &-title {
        font-weight: bold;
        margin-bottom: 8px;
        span {
          position: relative;
          display: inline-block;
          background-color: #32c832;
          border-radius: 50%;
          width: 13px;
          height: 13px;
          margin-right: 3px;
          bottom: -1px;
          svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 10px;
            height: 10px;
            path {
              fill: #fff;
            }
          }
        }
      }
      &-content {}
    }
  }

  &-discounts-gifts-wrapper {
    @media (min-width: $minMd) and (max-width: 979px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      & > div {
        flex: 0 0 49%;
        height: max-content;
      }
    }
  }

}
