.Lightbox-video {

  .modal-content {
    padding: 40px 20px 20px 20px;
  }

  .close {
    border: none;
    background-color: transparent;
    position: absolute;
    top: 0;
    right: 15px;
    cursor: pointer;
    span {
      font-size: 35px;
      line-height: 30px;
    }
    &:focus {
      outline: none;
    }
  }

  &-wrapper {
    position: relative;
    img {
      width: 100%;
      height: auto;
    }
    iframe {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}
