.Essentiel {
  overflow: hidden;
  padding-top: 20px;
  margin-bottom: 20px;

  &-content {
    margin-top: 19px;
  }

  &-title-h3 {
    margin-bottom: 15px;
    font-size: 24px;
  }

  .swiper-container {
    overflow: visible;
  }

  .swiper-pagination {
    position: initial;
    margin: 15px 0;
    @media (min-width: $minLg) {
      display: none;
    }
  }

  .swiper-pagination-bullet {
    margin: 0 4px;
    opacity: 1;

    &-active {
      background: $colorOrange;
      border-radius: initial;
    }
  }

  &-list {
    @media (min-width: $minLg) {
      display: block;
    }
  }

  &-item {
    @media (min-width: $minLg) {
      display: flex;

      /**/
      padding: 10px 0;
      .Essentiel-photo {
        padding-right: 10px;
      }
      .Essentiel-content {
        padding-left: 10px;
      }
      /**/


      &.align-reverse {
        .Essentiel-photo {
          order: 2;
          padding-left: 10px;
          padding-right: 0;
        }
        .Essentiel-content {
          padding-left: 0;
          padding-right: 10px;
        }
      }

      &:last-child {
        padding-bottom: 20px;
      }

    }
  }

  &-photo {
    img {
      width: 100%;
      height: auto;
    }

    @media (min-width: $minLg) {
      flex: 0 0 50%;
      height: 100%;
    }
  }

  &-content {
    @media (min-width: $minLg) {
      flex: 0 0 50%;
    }
  }
}



