.noUi-target.noUi-horizontal {
  height: 5px;
  margin-top: 25px;
  margin-bottom: 35px;
  border-radius: 0;
  border: none;
  box-shadow: none;
  background-color: $colorGrayLight;
  padding-left: 20px;

  .noUi-handle {
    width: 20px;
    height: 20px;
    top: -7px;
    border: 2px solid #000;
    border-radius: 50%;
    background-color: #fff;
    &:focus {
      outline: none;
    }
    &::before,
    &::after {
      width: 0;
      height: 0;
      background: transparent;
    }
    &.noUi-handle-lower {
      right: 0;
    }
    &.noUi-handle-upper {
      right: 0;
    }
  }

  .noUi-connect {
    background-color: $colorOrange;
  }

}
