.AvailabilityLabel {
  background-color: #fff;
  display: flex;
  font-size: 12px;
  font-weight: bold;
  padding: 10px;
  text-align: left;
  &.comingsoon {
    border: 2px solid $colorBlue;

    svg {
      path {
        fill: $colorBlue;
      }
    }
  }

  &.unavailable {
    border: 2px solid $colorRed;
  }

  &.lessthan {
    border: 1px solid $colorGrayLine;
    .colR > span {
      color: $colorOrange;
    }
  }

  .colL {
    padding-right: 5px;
  }

  .colR {
    padding: 2px 0;
  }

  svg {
    width: 16px;
    height: 16px;
  }
}
