.DiscountCard {

  padding: 10px;
  border-bottom: 1px solid #000;
  background-color: $colorYellow;


  &-header {
    display: flex;
    justify-content: space-between;
  }

  &-colRight {
    background-color: red;
    position: relative;
    span {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      cursor: pointer;
      svg {
        width: 30px;
        height: 30px;
      }
    }
  }

  .reset-btn {
    line-height: normal;
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;

    &:focus {
      outline: none;
    }
  }

  &-title {
    font-weight: bold;
    display: flex;
    align-items: center;
  }

  &-icon {
    font-size: 0;
    margin-right: 5px;
    svg {
      width: 20px;
      margin-top: -5px;
      path {
        fill: #000;
      }
    }
  }

  &-subtitle {
    font-weight: bold;
    display: flex;

    .Counter {
      margin-top: 4px;
      background-color: #fff;
      .Counter-display {
        font-size: 14px;
        background-color: #fff;
      }
      .Counter-separator {
        //font-size: 0;
        //opacity: 0;
      }
    }

  }

  &-dropdown {
    font-size: 14px;
    button {
      svg {
        width: 25px;
      }
    }
  }

  .collapse-content {
    margin-top: 10px;
  }

  &:last-child {
    margin-bottom: 20px;
    border-bottom: none;
  }

}
