.RatingStars {
  display: flex;
  .star {
    margin-right: 5px;
  }
  svg {
    margin: 2px;
    width: 15px;
    height: 15px;
    polygon {
      fill: $colorGrayLine;
    }
  }
  .active {
    svg {
      polygon {
        fill: $colorOrange;
      }
    }
  }
  &-reviews {
    margin-left: 7px;
    font-weight: bold;
    font-size: 14px;
    line-height: 19px;
  }
}
