.PageTitle {
  .breadcrumb {
    display: none;
    @media (min-width: $minMd) {
      display: flex;
      padding: 0.625rem 0 0 0 !important;
    }
    .breadcrumb-item + .breadcrumb-item::before {
      content: " ";
      background-image: url("/src/assets/icons/icon-angle-right.svg");
      background-repeat: no-repeat;
      background-size: 12px;
      transform: translateX(5px);
      min-width: 12px;
      min-height: 12px;
    }
  }

  &-backBtn {
    display: flex;
    padding: 0.625rem 0 0 0 !important;
    font-size: 14px;
    font-weight: bold;
    svg {
      width: 15px;
      height: 15px;
      margin-bottom: 4px;
      margin-right: 4px;
    }
  }

  .h1-like {
    font-size: 40px !important;
    margin: 17px 0 30px 0 !important;
  }

}
