.MenuDesktop {
  display: none;
  @media (min-width: $minLg) {
    display: block;
  }

  background-color: #000;

  & > .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-brand {
    padding: 25px 0;
    display: inline-block;
    position: relative;
    &-large {
      display: block;
      svg {
        width: 50px;
        height: 50px;
      }
    }
    &-small {
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      svg {
        width: 50px;
        height: 50px;
        opacity: 0;
        visibility: hidden;
      }
    }
  }


  /* ANIMATION */
  &.largeH {
    .MenuDesktop-brand {
      transition: 0.5s;
      padding-top: 25px;
      padding-bottom: 25px;
      &-small > svg {
        transition: 0.5s;
        opacity: 0;
        visibility: hidden;
        width: 50px;
        height: 50px;
      }
      &-large > svg {
        transition: 0.5s;
        opacity: 1;
        visibility: visible;
        width: 50px;
        height: 50px;
      }
    }
    .MenuDesktop-userNav-link, .MenuDesktop-userNav-loggedUser {
      transition: 0.5s;
      padding-top: 30px;
      padding-bottom: 27px;
    }
    .MenuDesktop-nav-link {
      transition: 0.5s;
      padding-top: 55px;
      padding-bottom: 0;
      &:hover {
        .MenuDesktop-orangeHoverDecoration {
          background-color: $colorOrange;
          span {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 3px;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 0 8px 8px 8px;
            border-color: transparent transparent $colorOrange transparent;
          }
        }
      }
    }
  }

  &-orangeHoverDecoration {
    position: relative;
    height: 3px;
    width: calc(100% + 30px);
    margin-left: -15px;
    background-color: #000;
  }

  &.largeH .MenuDesktop-orangeHoverDecoration {
    margin-top: 22px;
  }

  &.smallH .MenuDesktop-orangeHoverDecoration {
    margin-top: 10px;
  }

  &.smallH {
    .MenuDesktop-brand {
      transition: 0.5s;
      padding-top: 15px;
      padding-bottom: 15px;
      &-small > svg {
        transition: 0.5s;
        opacity: 1;
        visibility: visible;
        width: 30px;
        height: 30px;
      }
      &-large > svg {
        transition: 0.5s;
        opacity: 0;
        visibility: hidden;
        width: 30px;
        height: 30px;
      }
    }
    .MenuDesktop-userNav-link, .MenuDesktop-userNav-loggedUser {
      transition: 0.5s;
      padding-top: 15px;
      padding-bottom: 13px;
    }
    .MenuDesktop-nav-link {
      transition: 0.5s;
      padding-top: 20px;
      padding-bottom: 0;
    }
  }
  /* --- */

  &-collapse {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  &-nav {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    list-style: none;
    margin: 0;
    padding: 0;
    font-weight: bold;
  }

  &-nav-item {
    &:first-child a {
      padding-left: 30px;
    }
  }

  &-nav-link {
    color: #fff;
    padding: 25px 15px 10px 15px;
    display: block;

  }

  &-userNav {
    display: flex;
    list-style: none;
    flex-direction: row;
    align-items: flex-end;
    margin: 0;
    padding: 0;
    &-item {
      cursor: pointer;
    }
    .user-connexion-btn {
      padding-right: 0;
      width: 50px;
      text-align: center;
    }
    &-link {
      padding: 30px 20px 10px 20px;
      display: block;
    }
    .MenuDesktop-userNav-loggedUser {
      display: block;
      padding: 30px 0 10px 0;
      font-weight: bold;
      color: $colorOrange;
    }
    &-svg {
      max-height: 26px;
      width: auto;
      position: relative;
      path {
        fill: #fff;
      }
    }
    .circle-number {
      width: 20px;
      height: 20px;
      position: absolute;
      right: 0;
      top: 0;
      transform: translate(35%, -50%);
      border-radius: 50%;
      background-color: $colorBlue;
      color: #fff;
      font-size: 12px;
      font-weight: bold;
      text-align: center;
      line-height: 20px;
      padding-left: 1px;
    }
  }

  &.smallH {
    .MenuDesktop-nav-panel {
      &.collapse {
        top: 60px;
      }
    }
  }
  &.largeH {
    .MenuDesktop-nav-panel {
      &.collapse {
        top: 100px;
      }
    }
  }

  &-nav-panel {
    &.collapse {
      display: block;
      position: absolute;
      width: 100vw;
      height: 0;
      left: 50%;
      transform: translateX(-50%);
      overflow: hidden;
      background-color: #fff;
      z-index: 200;
      a {
        padding: 0;
      }
      .collapse-hr-line {
        width: 100%;
        height: 1px;
        background-color: $colorGrayLine;
      }
    }
  }

  &-subHeader {
    margin-top: 20px;
    margin-bottom: 30px;
    &-link {
      font-size: 20px;
      transition: 0.1s;
      svg {
        width: 15px;
        padding-bottom: 2px;
        polygon {
          fill: $colorOrange;
        }
      }
    }
  }

  &-subNav {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    margin-left: -10px;
    margin-right: -10px;
  }

  &-subItem {
    flex: 0 0 25%;
    padding: 0 10px;
    display: inline-block;
    & > a {
      font-size: 18px;
      display: flex;
      svg {
        width: 30px;
        height: 30px;
        padding-bottom: 5px;
        padding-right: 5px;
      }
    }
    & > img {
      max-width: 100%;
    }
  }

  &-subSubNav {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &-subSubItem {
    font-weight: normal;
    margin: 12px 0;
    svg {
      vertical-align: middle;
    }
  }

  .item-bold {
    font-weight: bold;
  }
}

.ConnexionCart {
  opacity: 0;
  visibility: hidden;

  transition: 0.5s;
  position: absolute;
  right: 0;
  min-width: 280px;
  max-width: 100%;
  padding: 20px;
  background-color: #fff;
  border-top: 3px solid $colorOrange;
  border-left: 1px solid $colorGrayLine;
  border-right: 1px solid $colorGrayLine;
  border-bottom: 1px solid $colorGrayLine;
  &-title {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  &-btn {
    margin-bottom: 20px;
  }
  &-link {
    font-size: 14px;
    font-weight: bold;
    text-decoration: underline;
  }
  &-close {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20px;
    height: 20px;
    cursor: pointer;
    span {
      position: absolute;
      right: -3px;
      width: 20px;
      height: 2px;
      background-color: #000;
      transform-origin: left center;
      &:first-child {
        transform: rotate(45deg);
        top: 2px;
      }
      &:last-child {
        transform: rotate(-45deg);
        top: 16px;
      }
    }
  }
  &.ConnexionCart-connected {
    .ConnexionCart-header {
      display: flex;
      margin-bottom: 14px;
    }
    .ConnexionCart-imgCircle {
      background-color: $colorGrayLight;
      border-radius: 50%;
      width: 50px;
      height: 50px;
      position: relative;
      & > span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
    .ConnexionCart-titleWrapper {
      padding-left: 20px;
    }
    .ConnexionCart-title {
      margin-bottom: 0;
    }
    .ConnexionCart-link {
      display: block;
      margin-bottom: 5px;
    }
    .ConnexionCart-btn {
      margin-bottom: 0;
      margin-top: 20px;
    }
  }
}

.user-connexion-item {
  position: relative;
}

.user-connexion-triangle {
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;

  position: absolute;
  left: calc(50% + 10px);
  transform: translateX(-50%);
  bottom: -1px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 8px 8px 8px;
  border-color: transparent transparent $colorOrange transparent;
}

@media (min-width: $minLg) {
  .largeH {
    .ConnexionCart {
      transition: 0.5s;
      top: 87px;
    }
  }

  .smallH {
    .ConnexionCart {
      transition: 0.5s;
      top: 59px;
    }
  }

  .open {
    .user-avatar-circle {
      display: inline-block;
      position: relative;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background-color: $colorGrayLight;
      margin-bottom: -4px;
      & > span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      svg {
        max-height: 17px;
        path {
          fill: $colorGrayDark;
        }
      }
    }
  }
}



