.ColorChart {
  display: flex;
  padding-left: 10px;
  margin: 5px 0;

  &-left {
    display: flex;
    align-items: center;
  }

  &-colorCircle {
    position: relative;
    width: 33px;
    height: 33px;
    border-radius: 50%;
    background-color: red;
    border: 6px solid #fff;
    margin-left: -15px;
    &:first-child {
      margin-left: -6px; /* equivalent a la largeur du border */
    }
    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
      background-clip: padding-box;
    }
  }

  &-right {
    padding-left: 15px;
    display: flex;
    align-items: center;
  }

}
