.CategoryFilter {
  position: relative;
  overflow: hidden;
  border-top: 1px solid $colorGrayLine;

  .swiper-wrapper, .swiper-container {
    margin-bottom: 0;
  }

  &-dark {
    border-top: 1px solid #000;
    background-color: #000;
  }


  &-list {
    display: flex;
    justify-content: center;
  }
  &-item {
    text-align: center;
    flex: 0 0 160px;
    padding: 10px 10px 0 10px;
    &.active,
    &:hover {
      .CategoryFilter-title {
        color: $colorOrange;
      }
      .CategoryFilter-active {
        background-color: $colorOrange;
        &-triangle {
          border-color: $colorOrange transparent transparent transparent;
        }
      }
    }
    &.active {
      .CategoryFilter-imgWrapper {
        .circle {
          border: 2px solid $colorOrange;
        }
      }
      .CategoryFilter-title {
        color: $colorOrange;
        font-weight: bold;
      }
    }
  }
  .swiper-container-horizontal { // suppression du hover sur mobile
    .CategoryFilter-item {
      &:hover {
        .CategoryFilter-active {
          background-color: $colorOrange;
        }
      }
    }
  }
  &-imgWrapper {
    position: relative;
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 35px;
      height: 35px;
    }
    .circle {
      margin: 5px auto;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: $colorGrayLight;
      transition: 0.5s;
    }
  }
  &-title {
    font-size: 14px;
    color: #000;
    margin-top: 6px;
    margin-bottom: 10px;
    transition: 0.5s;
    font-weight: bold;
  }
  &-dark {
    .CategoryFilter-title {
      color: #fff;
    }
  }
  &-item {
    &:nth-child(1) {
      .CategoryFilter-imgWrapper img {
        top: 55%;
      }
    }
    &:nth-child(2) {
      .CategoryFilter-imgWrapper img {
        top: 52%;
      }
    }
    &:nth-child(3) {
      .CategoryFilter-imgWrapper img {
        top: 55%;
      }
    }
    &:nth-child(4) {
      .CategoryFilter-imgWrapper img {
        top: 55%;
      }
    }
    &:nth-child(5) {
      .CategoryFilter-imgWrapper img {
        top: 55%;
      }
    }
  }
  &-active {
    width: 100%;
    height: 3px;
    background-color: transparent;
    transition: 0.5s;
    &-triangle {
      transition: 0.5s;
      position: absolute;
      bottom: -8px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 8px 7px 0 7px;
      border-color: transparent transparent transparent transparent;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &-swiperPrev,
  &-swiperNext {
    position: absolute;
    top: -4px;
    width: 28px;
    height: 100%;
    z-index: 1;
    transition: 0.5s;
    &-icon {
      position: absolute;
      top: 50%;
      left: 0px;
      transform: translateY(-50%);
      display: block;
      svg {
        width: 23px;
        height: 23px;
        polygon {
          fill: $colorGrayDark;
        }
      }
    }
    &:focus {
      outline: none;
    }
  }

  &-swiperPrev {
    left: 0;
  }

  &-swiperNext {
    right: 0;
  }

  .swiper-container {
    overflow: visible;
  }

  &-bottomLine {
    background-color: #fff;
    height: 8px;
    border-top: 1px solid $colorGrayLine;
  }

  &-dark {
    .CategoryFilter-bottomLine {
      border-top: none;
    }
  }

}
