.WindowFilter-container {
  overflow: hidden;
  width: 100%;
}

.WindowFilter {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 0;
  height: calc(100vh - 50px);
  width: 362px;
  max-width: 100%;
  background-color: #fff;
  z-index: 1000;
  border-right: 1px solid $colorGrayLine;
  //overflow-y: scroll;

  @media (min-width: $minLg) {
    &.largeH {
      height: calc(100vh - 141px);
      transition-property: top;
      transition-duration: 0.5s;
      top: 141px;
    }
    &.smallH {
      height: calc(100vh - 61px);
      transition-property: top;
      transition-duration: 0.5s;
      top: 61px;
    }
  }

  /* ANIM */
  transform: translateX(-362px);


  & > form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }


  &-header {
    //background-color: $colorYellow;
    padding: 20px 15px 0 20px;

    &-text {
      font-weight: bold;
      span {
        color: $colorOrange;
      }
    }

    &-backBtn {
      margin-bottom: 12px;
      font-size: 14px;
      font-weight: bold;
      cursor: pointer;
      svg {
        width: 15px;
        height: 15px;
        margin-bottom: 2px;
      }
    }
  }

  &-body {
    position: relative;
    padding-left: 20px;
    &-scrollable {
      padding: 20px 20px 20px 0;
      overflow-y: scroll;
      overflow-x: hidden;
    }
    &-gradientTop {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 20px;
      width: 100%;
      background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
    }
    &-gradientBottom {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 20px;
      width: 100%;
      background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
    }
  }

  &.largeH {
    .WindowFilter-body-scrollable {
      transition: 0.5s;
      height: calc(100vh - 310px);
    }
  }
  &.smallH {
    .WindowFilter-body-scrollable {
      transition: 0.5s;
      height: calc(100vh - 230px);
    }
  }

  &-footer {
    padding: 0 20px;
  }
  &-price {
    &-title {
      font-size: 18px;
      font-weight: bold;
      padding-bottom: 15px;
    }
    .price-inputs {
      margin-bottom: 10px;
      .form-group {
        margin-bottom: 10px;
      }
      label[for="inputPriceMin"],
      label[for="inputPriceMax"] {
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);
        margin: 0;
      }
      input#inputPriceMin,
      input#inputPriceMax {
        padding-right: 50px;
      }
      .a-letter {
        line-height: 40px;
        padding-left: 10px;
      }
      .form-row > span {
        padding-left: 5px;
        margin-bottom: 10px;
        font-weight: bold;
      }
    }
  }
  &-buttons {
    font-size: 0;
    padding: 20px 0;
    .btn {
      margin-right: 20px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  ul {
    list-style: none;
    padding-left: 0;
    margin: 0;
    li {
      margin-bottom: 20px;
    }
  }

  &-accordion {
    label {
      font-weight: normal;
    }
    .card {
      border-top-width: 1px;
      border-bottom-width: 1px;
    }
  }

  // Boosted
  .card-body {
    border-top: 1px solid $colorGrayLine;
    padding-top: 20px;
  }
  .btn-link {
    font-size: 18px;
  }

  // Correction couleur checkbox + radio

  label.custom-control-label,
  span.custom-control-label {
    &::before {
      border-color: $colorGrayLine;
    }
  }
}
