body { // on compense le fait que le menu soit passe en fixed...
  padding-top: 50px;
  @media (min-width: $minLg) {
    padding-top: 140px;
  }
}

/// boosted
.container-fluid {
  //background-color: $colorGrayLight;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 40px;
}

.accordion-layout {
  .o-tab-content, a {
    outline: none;
  }

  .o-tab-content {
    padding-right: 0 !important;
  }
}

.container {
  position: relative;
}

.col-custom-45 {
  flex: 0 0 45%;
  max-width: 45%;
  padding-right: 5px;
  padding-left: 5px;
  @media (min-width: 768px) {
    padding-right: 10px;
    padding-left: 10px;
  }
}

.col-custom-55 {
  flex: 0 0 55%;
  max-width: 55%;
  padding-right: 5px;
  padding-left: 5px;
  @media (min-width: 768px) {
    padding-right: 10px;
    padding-left: 10px;
  }
}

/* SWIPER PAGINATION */

.swiper-pagination.swiper-pagination-bullets {
  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin: 1px 10px;
    background: $colorGrayLine;
    opacity: 1;
    &-active {
      width: 12px;
      height: 12px;
      border-radius: 50%;
      margin: 0px 10px;
      background: $colorOrange;
    }
  }
}
