.OffreVariation-navbar {
  display: flex;
  justify-content: space-between;
  line-height: 30px;
  @media (min-width: $minLg) {
    display: flex;
    justify-content: space-between;
  }

  .btnNavBar {
    display: flex;
    align-items: center;
    cursor: pointer;

    span {
      svg {
        width: 20px !important;
        margin: 0 5px;
      }
    }

    &:focus {
      outline: none;
    }
  }

  .btnNavBar-prev,
  .btnNavBar-next {
    transition: 0.5s;

    svg {
      polygon {
        fill: #000000;
      }
    }

    @media (min-width: $minLg) {
      opacity: 1;
      &.swiper-button-disabled {
        opacity: 0;
      }
    }

    &.swiper-button-disabled {
      svg {
        polygon {
          fill: #A6A6A6;
        }
      }
    }
  }

  &-wrapper {
    padding-top: 5px !important;
    padding-bottom: 5px !important;
    border-top: 1px solid $colorGrayLine;
    border-bottom: 1px solid $colorGrayLine;
    @media (min-width: $minLg) {
      border: none;
      padding-top: 0 !important;
      padding-bottom: 0 !important;
    }
  }

}

@media (min-width: $minLg) {
  .OffreVariation-navbar-1-items,
  .OffreVariation-navbar-2-items,
  .OffreVariation-navbar-3-items,
  .OffreVariation-navbar-4-items {
    .btnNavBar-prev,
    .btnNavBar-next {
      display: none;
    }
  }
}
