.CarteSim {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  &-image {
    max-width: 45px;
    margin-right: 10px;
    img {
      max-width: 100%;
    }
  }

  &-texte {
    font-weight: bold;
    font-size: 16px;
  }

  &-number {
    color: $colorOrange;
    font-size: 12px;
  }

  &-subtitle {
    font-weight: initial;
    font-size: 14px;
  }
}
