.Tab {
  display: flex;
  height: 30px;
  min-height: 30px;
  width: 100%;
  justify-content: space-between;
  border-bottom: 2px solid #fff;
  background-color: #fff;

  /* variantes de couleur */
  &-orange {
    border-bottom: 2px solid $colorOrange;
    .Tab-triangle {
      //background-color: $colorOrange;
    }
    .Tab-left {
      background-color: $colorOrange;
      color: #fff;
    }
  }
  &-blue {
    border-bottom: 2px solid $colorBlueCelestial;
    .Tab-triangle {
      //background-color: $colorBlueCelestial;
    }
    .Tab-left {
      background-color: $colorBlueCelestial;
    }
  }
  &-green {
    border-bottom: 2px solid $colorGreen;
    .Tab-triangle {
      //background-color: $colorGreen;
    }
    .Tab-left {
      background-color: $colorGreen;
    }
  }
  &-yellow {
    border-bottom: 2px solid $colorYellow;
    .Tab-triangle {
      //background-color: $colorYellow;
    }
    .Tab-left {
      background-color: $colorYellow;
    }
  }

  &-left {
    display: flex;
    position: relative;
    line-height: 28px;
    padding:  0 16px 0 8px;
    font-weight: bold;
  }

  &-icon {
    font-size: 0;
    padding-right: 4px;
    svg {
      width: 15px;
      height: 15px;
      path {
        fill: #fff;
      }
    }
  }

  &-triangle {
    position: absolute;
    top: -1px;
    right: -1px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 15px 30px 0;
    border-color: transparent #fff transparent transparent;
  }

  &-right {
    background-color: #fff;
  }

  &-additional {
    font-size: 14px;
    color: $colorOrange;
    font-weight: bold;
    line-height: 28px;
  }
}
