.MenuTop {
  display: none;
  overflow: hidden;
  @media (min-width: $minLg) {
    display: block;
  }

  height: 40px;
  background-color: #000;
  font-size: 14px;
  font-weight: bold;
  border-bottom: 1px solid #333333;

  & > .container {
    display: flex;
    justify-content: space-between;
  }

  &-main {
    list-style: none;
    margin: 0;
    padding: 0;
    & > li {
      display: inline-block;
      & > a {
        color: #fff;
        line-height: 40px;
        padding: 10px;
        &.active,
        &:hover {
          color: $colorOrange;
        }
      }
      &:first-child > a {
        padding-left: 0;
      }
    }
  }

  &-blockRight {
    display: flex;
    justify-content: space-between;
  }

  &-secondary {
    list-style: none;
    margin: 0;
    padding: 0 30px;
    & > li {
      display: inline-block;
      & > a {
        color: #fff;
        line-height: 40px;
        padding: 10px;
        &.active,
        &:hover {
          color: $colorOrange;
          svg {
            path {
              fill: $colorOrange;
            }
          }
        }
        svg {
          width: 22px;
          height: 22px;
          padding-bottom: 2px;
          path {
            fill: #fff;
          }
        }
      }
    }
  }

  &-lang {
    list-style: none;
    margin: 0;
    padding: 0;
    & > li {
      display: inline-block;
      & > a {
        color: #fff;
        line-height: 40px;
        padding: 5px;
        &.active,
        &:hover {
          color: $colorOrange;
        }
      }
      &:last-child > a {
        padding-right: 0;
      }
    }
  }
}
