.OffreItem {
  display: flex;
  position: relative;
  min-height: 180px;
  height: 100%;
  width: 100%;
  background: #fff;
  border: 2px solid $colorGrayLine;
  padding-right: 60px;

  &:hover {
    color: #000;
  }

  .col-picto {
    flex: 0 0 40%;

    display: flex;
    justify-content: center;
    align-items: center;

    img {
      max-width: 100%;
    }
  }

  .col-texte {
    flex: 0 0 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
  }

  &-title {
    font-size: 24px;
    padding: 10px 0 7px 10px;
    font-weight: bold;
  }

  &-content {
    font-size: 16px;
    padding: 0 10px 14px 10px;
    padding-right: 60px;

  }

  &-forfait {
    padding: 0 10px 10px 10px;
  }

  &-counter {
    padding: 10px 10px 0 10px;
    display: flex;

    .Counter {
      &-display {
        width: 30px;
        height: 30px;
        line-height: 30px;
        font-size: 16px;
      }
      &-separator {
        padding: 0 3px;
        font-size: 10px;
        line-height: 30px;
      }
    }
  }

  &-price {
    padding: 0 10px 10px 10px;
    .underline {
      text-decoration: line-through;
      text-transform: uppercase;
      color: $colorGrayDark;
    }
    .pageStyle-boldOrange {
      font-size: 20px;
      text-transform: uppercase;
    }
    .little {
      font-size: 14px;
      color: $colorOrange;
    }
  }
}
