.ColorCircle {
  position: relative;
  width: 16px;
  height: 16px;
  margin: 12px;
  display: inline-block;
  border-radius: 50%;

  @media (min-width: $minMd) {
    margin: 12px 17px;
  }

  &-externe {
    display: none;
    width: 26px; // ce chiffre doit être pair pour le centrage
    height: 26px; // ce chiffre doit être pair pour le centrage
    border: 2px solid #000000;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }



  .ColorCircle-externe {
    display: block;
  }

  .ColorCircle-warning {
    bottom: -8px;
    right: -8px;
  }

  &-warning {
    display: block;
    position: absolute;
    bottom: -5px;
    right: -5px;
    width: 18px;
    height: 18px;
  }

  &.isActive {
    .ColorCircle-externe {
      border: 2px solid $colorOrange;
    }
  }

}
