.TerminauxItem {
  display: block;
  padding: 10px 10px 10px 0;
  background-color: #fff;
  border: 2px solid $colorGrayMedium;
  height: 100%;
  position: relative;

  &:hover {
    color: inherit;
  }

  .col-wrapper {
    display: flex;

    .col-custom {
      &:first-child {
        flex: 0 0 45%;
        text-align: center;

        .image-wrapper {
          position: relative;
        }

        img {
          margin-bottom: 20px;
        }

        .text-wrapper {
          text-align: left;
          padding-left: 10px;
        }
      }

      &:last-child {
        flex: 0 0 55%;
      }
    }
  }

  &-das {
    font-size: 14px;
    color: #4a4a4a;
  }

  &-compare {
    margin-top: 10px;

    .custom-control-label::before {
      border-color: $colorGrayLine;
    }

    .custom-control-description {
      font-weight: bold;
    }

    /*
    .custom-control-label {
      &::before {
        top: 50%;
        transform: translateY(-50%);
      }
      &::after {
        top: calc(28% + .4rem) !important;
        transform: rotate(-45deg) translateY(-50%) !important
      }
    }

    .custom-control-description {
      display: block;
      padding: 10px 0;
    }
    */
  }

  &-title,
  &-subtitle {
    font-size: 24px;
    font-weight: bold;
  }

  .RatingStars {
    margin-top: 8px;
  }

  &-price {
    margin: 12px 0;

    &-amount {
      font-size: 20px;
      color: $colorOrange;
      font-weight: bold;
    }

    &-crossed {
      text-decoration: line-through;
      color: #4a4a4a;
    }
  }

  &-payback {
    margin-left: 10px;
    margin-bottom: 10px;
    margin-top: 10px;
    background-color: $colorYellow;
    display: flex;
    font-weight: bold;

    &-colL {
      padding: 8px 10px;

      svg {
        width: 15px;
        height: 15px;
        path {
          fill: #000;
        }
      }
    }

    &-colR {
      padding: 10px 10px 10px 0;
    }
  }

  &-colors {
    margin: 16px 0;

    &-basic {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
    }

    .ColorCircle {
      margin: 0;
      margin-right: 28px;

      &.isActive {
        margin-left: 5px;
        margin-right: 31px;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    &-more {
      font-size: 12px;
      font-weight: bold;
      color: #4a4a4a;
    }
  }

}
