.OffreVariation-1item {

  .OffreVariation-isActive {
    transform: none;
  }

  &-summary {
    margin-top: 29px;
  }

  .Offre-sim {
    .pageStyle-hr-line {
      background-color: transparent;
      margin: 3px 0;
    }
  }

  .OffreVariation-forfait {
    padding: 0;
    border: none;
    @media (min-width: $minLg) {
      padding: 10px 20px;
      border: 1px solid #cccccc;
      .pageStyle-hr-line,
      .Warning {
        display: block
      }
    }
    .pageStyle-hr-line,
    .Warning {
      display: none;
    }
  }

  .OffreVariation-header {
    width: 100% !important;
  }
}
