.pageStyle-hr-line {
  width: 100%;
  height: 1px;
  background-color: $colorGrayLine;
  margin: 15px 0;
}

// Boutons
.pageStyle-btn-forfait {
  background: $colorOrange;
  color: #fff;
  padding: 10px;
  margin: 20px 0;
  text-align: center;
  display: block;

  &:hover {
    color: #fff;
  }
}

.pageStyle-btn-mobile {
  text-align: center;
  display: block;
  border: 2px solid #000;
  font-weight: bold;
  padding: 3px;
  margin: 20px 0;

  img {
    transform: scale(0.7);
  }

  @media (max-width: $maxMd) {
    margin: 10px 0;
  }
}

// Styles
.pageStyle-bold {
  font-weight: bold;
}

.pageStyle-boldOrange {
  color: $colorOrange;
  font-weight: bold;
}

.pageStyle-through {
  text-decoration: line-through;
  color: $colorGrayDark;
}

.pageStyle-h2-like {
  font-size: 30px;
  margin-bottom: 20px;
}


