.ReviewsSection {
  padding-top: 10px;

  .ReviewsSection-colL {
    display: none;
    flex: 0 0 25%;
    max-width: 25%;
    @media (min-width: $minLg) {
      display: block;
    }
  }

  .ReviewsSection-colR {
    flex: 0 0 100%;
    max-width: 100%;
    @media (min-width: $minLg) {
      flex: 0 0 75%;
      max-width: 75%;
    }
  }

  &-average-title {
    font-size: 18px;
    font-weight: bold;
  }
  &-average-score {
    font-size: 72px;
    line-height: 1em;
    font-weight: bold;
    color: $colorOrange;
  }
  &-average-stars {
    margin-bottom: 25px;
    .RatingStars {
      display: block;
      margin: auto;
    }
  }

  &-details {
    &-row {
      text-align: left;
      display: flex;
      margin-bottom: 15px;
    }
    &-colL {
      font-size: 28px;
      font-weight: bold;
      padding-right: 10px;
    }
    &-colR {
      flex: 1;
    }
    &-stars {
      margin-bottom: 2px;
    }
    .progress {
      height: 3px;
      margin-bottom: 2px;
    }
    &-nbReviews {
      font-size: 12px;
    }
  }

  &-leave-comment {
    margin-bottom: 20px;
    width: 100%;
    @media (min-width: $minSm) {
      width: auto;
    }
  }

  &-review {
    margin-bottom: 20px;
  }

  &-review-top {
    margin-bottom: 3px;
  }
  &-review-title {
    display: inline-block;
    margin-right: 22px;
  }
  &-review-date {
    display: inline-block;
    font-weight: bold;
    font-size: 18px;
  }
  &-review-stars {
    margin-bottom: 5px;
  }
  &-review-comment {
    margin-top: 5px;
    margin-bottom: 8px;
  }
  &-review-readMore {
    .reset-btn {
      font-size: 12px;
      color: $colorGrayDark;
      line-height: 30px;
      border: none;
      padding: 0;
      background-color: transparent;
      cursor: pointer;
      &:focus {
        outline: none;
      }
      svg {
        width: 20px;
        polygon {
          fill: $colorGrayDark;
        }
      }
    }
    .collapse-content {
      margin-bottom: 3px;
    }
  }
  &-review-likes {
    display: flex;
    font-size: 14px;
    font-weight: bold;
    svg {
      width: 15px;
      margin-right: 2px;
    }
  }
  &-review-thumbUp {
    margin-right: 15px;
    svg {
      padding-bottom: 5px;
    }
  }

  &-moreReviews {
    width: 100%;
    @media (min-width: $minSm) {
      width: auto;
    }
  }

}
