.TerminauxInformations {

  &-rating {
    &-wrapper {
      display: inline-block;
    }
  }

  &-rating {
    margin: 10px 0;
  }

  &-reviews {
    margin-left: 5px;
    font-weight: bold;
    text-decoration: underline;
  }

  &-description {
    font-size: 18px;
    font-weight: bold;
    margin: 20px 0;
  }

  &-specifications {
    padding-left: 20px;
    margin-bottom: 22px;

    & > li {
      padding-left: 5px;
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }

      &:nth-child(n+3) {
        display: none;
        @media (min-width: 768px) {
          display: list-item;
        }
      }

    }
  }

  &-addToComparator {
    font-weight: bold;

    svg {
      width: 15px;
      height: 15px;
    }
  }

  &-seeMoreBtn {
    cursor: pointer;
    text-decoration: underline;
    line-height: 28px;
    font-weight: bold;
    margin-bottom: 20px;
    &-plus {
      svg {
        width: 20px;
        height: 20px;
        padding-right: 2px;
        padding-bottom: 2px;
      }
    }
    &-dropdown {
      svg {
        width: 28px;
        height: 28px;
      }
    }
  }

}
