.Offre {

  &-container {
    padding: 0 !important;
    margin-bottom: 40px;
    max-width: 980px !important;
    @media (min-width: $minLg) {
      max-width: 960px !important;
    }
  }

  &-sim {
    display: block;
  }

  &-swiper {
    width: 100%;
    overflow: hidden;

    .swiper-container {
      margin-bottom: 45px;
      padding: 30px 20px;
      margin: 0 -10px;
    }

    .swiper-pagination.swiper-pagination-bullets {
      left: 50%;
      transform: translateX(-50%);
      .swiper-pagination-bullet {
        margin: 2px 5px;
        &-active {
          width: 14px;
          height: 14px;
          margin: 0px 5px;
        }
      }
    }
  }

  &-variation {
    @media (min-width: $minLg) {
      display: flex;
      margin: 0 -20px;

      .OffreVariation {
        flex: 1;
        margin: 0 20px;
      }
    }
  }

  &-tuile {
    display: block;
    margin-bottom: 15px !important;

    @media (min-width: $minLg) {
      display: none;
      margin-bottom: 0;
    }
  }

  ///// Deuxième partie de la page ! TODO : @media (min-width: $minLg) {
  &-detail {
    display: flex;
    flex-wrap: wrap;
    @media (min-width: $minLg) {
      display: flex;
      flex-wrap: nowrap;
      justify-content: space-between;
    }
  }

  &-detail-offreVariation {

    display: none;

    @media (min-width: $minLg) {
      display: block;
    }

  }

  &-forfait-informations {
    .pageStyle-boldOrange,
    .engagement,
    .pageStyle-bold,
    .pageStyle-hr-line,
    .Warning {
      display: none;
      @media (min-width: $minLg) {
        display: block;
      }
    }
    .Tab {
      display: none;
      @media (min-width: $minLg) {
        display: flex;
      }
    }
    .OffreVariation-forfait {
      border: none;
      padding: 0;
      @media (min-width: $minLg) {
        border: 1px solid #cccccc;
        padding: 10px 20px;
      }
    }
  }

  &-description {
    order: 2;

    .sim {
      display: none;
    }

    @media (min-width: $minLg) {
      .sim {
        display: block;

      }
      order: initial;
    }
  }

  &-buttons {
    flex: 0 0 100%;

    @media (min-width: $minLg) {
      flex: 0 0 40%;
    }

    .pageStyle-btn-forfait {
      display: none;
      @media (min-width: $minLg) {
        display: block;
      }
    }

    .pageStyle-btn-mobile {
      display: none;
      @media (min-width: $minLg) {
        display: block;
      }
    }
  }
}

